.practice {
  margin-top: 70px;

  @media (max-width: $mobile) {
    margin-top: 17px;
  }
}

.practice__wrapper {
  display: grid;

  grid-template-areas:
    "info      sidebar"
    "skills-hard    sidebar"
    "skills-hard    payment"
    "skills-soft .";

  max-width: 1460px;
  margin-right: auto;
  margin-left: auto;

  padding: 0 20px;

  @media (max-width: $tablet) {
    column-gap: 50px;
  }

  @media (max-width: $mobile) {
    grid-template-areas:
    "info         ."
    "sidebar      sidebar"
    "skills-hard   skills-hard"
    "skills-soft   skills-soft"
    "payment      payment";
  }
}

.practice__skills-hard {
  grid-area: skills-hard;

  @media (max-width: $mobile) {
    width: 100%;
  }
}

.practice__skills-soft {
  grid-area: skills-soft;

  @media (max-width: $mobile) {
    width: 100%;
  }
}

.practice__title {
  margin-top: 0;
  margin-bottom: 24px;
  font-size: 27px;
  font-weight: bold;
  line-height: 1.2;

  @media (max-width: $mobile) {
    grid-area: title;
    margin-bottom: 7px;
    padding-top: 18px;
    border-top: 1px solid $black;
    font-size: 18px;
    line-height: 24px;
  }
}

.practice__price {
  margin-top: 0;
  margin-bottom: 25px;
  font-size: 24px;
  line-height: 1.2;
  color: #BAC0C5;

  span {
    padding-left: 20px;
    font-size: 25px;
    font-weight: bold;
    line-height: 1.2;
    color: $black;
  }

  @media (max-width: $mobile) {
    grid-area: price;
    margin-bottom: 13px;

    font-size: 9px;
    line-height: 9px;
    font-weight: 400;

    span {
      display: block;
      padding-top: 2px;
      padding-left: 0;

      font-size: 15px;
      font-weight: 400;
      line-height: 20px;
    }
  }
}

.practice__description {
  margin-top: 0;
  margin-bottom: 89px;

  font-size: 20px;
  line-height: 1.8;

  @media (max-width: $mobile) {
    grid-area: desc;
    margin-bottom: 52px;

    font-size: 15px;
    line-height: 24px;
  }
}

.practice__info {
  grid-area: info;

  @media (max-width: $mobile) {
    grid-column: 1 / 3;
    grid-row: 1 / 2;

    display: grid;
    grid-template-areas:
     "price  ."
     "title  title"
     "desc   desc";
  }
}

.practice__hard {
  grid-area: hard;
  margin-bottom: 67px;
}

.practice__soft {
  grid-area: soft;
}

.practice__sidebar {
  grid-area: sidebar;
}

.practice__partner {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 16px rgba(#000000, 0.1);

  @media (max-width: $mobile) {
    margin-bottom: 20px;
  }
}

.practice__partner-img-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  border-radius: 10px;
  background-color: $bg-light;
}

.practice__partner-img {
  width: auto;
  height: 48px;
}

.practice__partner-title {
  margin-left: 10px;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.2;

  @media (max-width: $mobile) {
    font-size: 14px;
    line-height: 19px;
  }
}

.practice__partner-link {
  color: $black;
  text-decoration: none;
  transition: color .3s;

  &:hover,
  &:focus {
    color: $main-color;
  }
}

.practice__details {
  margin-bottom: 23px;
}

.practice__details-list {
  @include list-reset;
}

.practice__details-item {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 20px;
  padding: 10px 20px 10px 85px;
  box-shadow: 0 0 16px rgba(#000000, 0.1);
  border-radius: 10px;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 32px);
    left: 10px;
    width: 64px;
    height: 64px;
    border-radius: 10px;
    background-color: $bg-light;
    background-image: url("../../img/clock-icon.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &--format::before {
    background-image: url("../../img/play-icon.svg");
  }

  @media (max-width: 1220px) {
    flex-direction: column;

    min-height: 84px;
  }

  @media (max-width: $mobile) {
    flex-direction: row;

    &::before {
      top: calc(50% - 22px);

      width: 44px;
      height: 44px;
    }

    min-height: 64px;
  }
}

.practice__details-title {
  font-size: 16px;
  font-weight: bold;
  line-height: 1.2;

  @media (max-width: 1220px) {
    margin-top: 0;
    margin-bottom: 10px;
  }

  @media (max-width: $mobile) {
    margin-top: 0;
    margin-bottom: 0;

    font-size: 14px;
    line-height: 19px;
  }
}

.practice__details-info {
  font-size: 19px;
  font-weight: bold;
  line-height: 1.2;
  color: $main-color;

  @media (max-width: 1220px) {
    margin-top: 0;
    margin-bottom: 0;
  }

  @media (max-width: $mobile) {
    margin-top: 0;
    margin-bottom: 0;

    font-size: 12px;
    line-height: 17px;
  }
}

.practice__invite {
  grid-area: payment;
  margin-bottom: 15px;

  @media (max-width: $mobile) {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    margin-bottom: 0;
  }
}

.practice__invite-btn {
  display: block;
  padding: 25px;
  font-size: 21px;
  font-weight: bold;
  line-height: 1.3;
  text-align: center;
  text-decoration: none;
  color: #fff;
  background-color: $main-color;
  border-radius: 10px;
  box-shadow: 0 3px 4px rgba(#000000, 0.16);
  transition: background-color .3s, box-shadow .3s;

  &:focus,
  &:hover {
    background-color: darken($main-color, 10);
    box-shadow: none;
  }

  @media (max-width: $mobile) {
    padding-top: 8px;
    padding-bottom: 12px;
    padding-right: 10px;
    padding-left: 10px;

    font-size: 13px;
    line-height: 17px;
  }
}

