.main-page-swiper {
  margin-top: 15px;
}

.main-page-swiper__wrapper {
  position: relative;
  max-width: 1480px;
  margin: 0 auto;
  padding: 15px 20px;

  @media (max-width: $mobile) {
    padding: 0 16px;
  }
}

.main-page-swiper__swiper {
  position: relative;
}

.main-page-swiper__swiper-slide {
  display: block;
  width: 303px;
  min-height: 212px;
  border-radius: 10px;
  // background-image: url("../img/main-slider-placeholder-mobile.jpg");
  background-image: url("https://cdn.pixabay.com/photo/2021/12/11/19/21/breakfast-6863479_960_720.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  &:last-child {
    margin-right: 0;
  }
  @media (min-width: $mobile) {
    background-image: url("../img/main-slider-placeholder.jpg");
  }

  // @media (max-width: $mobile) {
  //   flex-basis: 353px;
  //   flex-shrink: 0;
  //   flex-wrap: nowrap;
  // }
}

.main-page-swiper__pagination {
  position: relative;
  &.swiper-pagination.swiper-pagination-bullets.swiper-pagination-horizontal {
    bottom: -25px;

    & .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background: $black;
      opacity: 1;
      margin: 0 2.5px;
    }
    & .swiper-pagination-bullet-active {
      background: $main-color;
    }
  }
}

