.search {
  flex-grow: 50;
  align-self: baseline;

  padding-top: 5px;

  @media (max-width: $mobile) {
    position: absolute;
    left: 0;
    top: 0;

    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;

    background-color: $white;

    z-index: 30;
  }
}

.search--hidden {
  @media (max-width: $mobile) {
    display: none;
  }
}

.search__form {
  position: relative;

  margin-left: 58px;
  margin-right: 102px;

  @media (max-width: $desktop) {
    margin-right: 70px;
    margin-left: 40px;
  }

  @media (max-width: $tablet) {
    margin-right: 30px;
    margin-left: 20px;
  }

  @media (max-width: $mobile) {
    width: 100%;
  }
}

.search__input {
  position: relative;
  width: 100%;
  height: 44px;
  padding: 0 160px 0 35px;
  border: 1px solid $main-color;
  border-radius: 10px;
  background-color: #F3F4F9;
  background-image: url('../../img/search-icon.svg');
  background-repeat: no-repeat;
  background-position: 11px center;
  background-size: 18px;

  &::placeholder {
    font-size: 13px;
    color: #4D4D4D;
  }

  @media (max-width: $tablet) {
    padding-right: 65px;
  }

  @media (max-width: $mobile) {
    background-image: none;
  }
}

.search__submit-btn {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 40px;
  padding-left: 39px;
  border: none;
  border-radius: 10px;

  font-size: 18px;
  font-weight: bold;
  line-height: 24px;

  color: $white;
  background-color: $main-color;
  transition: background-color .3s;

  cursor: pointer;

  &:focus,
  &:hover {
    background-color: darken($main-color, 10);
  }

  @media (max-width: $tablet) {
    padding-right: 5px;
    padding-left: 5px;
  }
}

.search__close-btn {
  display: none;

  @media (max-width: $mobile) {
    position: absolute;
    left: 3px;
    top: calc(50% - 15px);

    display: block;
    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    background-color: transparent;

    cursor: pointer;

    &::after {
      content: "";
      position: absolute;

      left: 0;
      top: calc(50% - 1px);

      width: 100%;
      height: 2px;
      transform: rotate(45deg);

      background-color: $black;
    }

    &::before {
      content: "";
      position: absolute;

      left: 0;
      top: calc(50% - 1px);

      width: 100%;
      height: 2px;
      transform: rotate(-45deg);

      background-color: $black;
    }
  }
}

